<template>
  <section>
    <!-- config main menu -->
    <v-card v-if="!configKey" flat>
      <v-list tile>
        <v-list-item
          v-for="(conf_value, conf_key) in configMenu"
          :key="conf_key"
          tile
          @click="$router.push('/config/' + conf_key)"
        >
          <v-list-item-icon style="margin-left: 15px">
            <v-icon large>{{ conf_value.icon }}</v-icon>
          </v-list-item-icon>
          <v-list-item-content>
            <v-list-item-title>{{ conf_value.label }}</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </v-list>
    </v-card>

    <!-- generic and module settings -->
    <v-card v-if="configKey" flat>
      <v-list two-line tile>
        <v-list-group
          v-for="(conf_value, conf_key) in configItems"
          :key="conf_key"
          no-action
        >
          <template #activator>
            <v-list-item>
              <v-list-item-avatar tile style="margin-left: -15px">
                <img
                  v-if="configKey == 'base'"
                  :src="require('../assets/' + conf_key + '.png')"
                  style="
                    border-radius: 5px;
                    border: 1px solid rgba(0, 0, 0, 0.85);
                  "
                />
                <img
                  v-if="
                    [
                      'player_providers',
                      'music_providers',
                      'metadata_providers',
                      'plugins',
                    ].includes(configKey)
                  "
                  :src="getProviderIcon(conf_key)"
                  style="
                    border-radius: 5px;
                    border: 1px solid rgba(0, 0, 0, 0.85);
                  "
                />
                <img
                  v-if="configKey == 'player_settings'"
                  :src="getProviderIcon(getPlayer(conf_key).provider_id)"
                  style="
                    border-radius: 5px;
                    border: 1px solid rgba(0, 0, 0, 0.85);
                  "
                />
                <v-icon v-if="configKey == 'security'">mdi-shield-lock</v-icon>
              </v-list-item-avatar>
              <v-list-item-content>
                <v-list-item-title v-if="conf_value['__name__']">{{
                  conf_value["__name__"]["label"]
                }}</v-list-item-title>
                <v-list-item-title v-else-if="configKey == 'player_settings'"
                  ><b>{{ getPlayer(conf_key).name }}</b></v-list-item-title
                >
                <v-list-item-title v-else style="text-transform: capitalize">{{
                  conf_key
                }}</v-list-item-title>
                <v-list-item-subtitle v-if="configKey == 'player_settings'">
                  <span
                    v-for="(value, name) in getPlayer(conf_key).device_info"
                    :key="name"
                  >
                    <b
                      ><span v-if="value">{{ name + ": " }}</span></b
                    ><span v-if="value">{{ value + "  " }}</span>
                  </span>
                </v-list-item-subtitle>
              </v-list-item-content>
            </v-list-item>
          </template>
          <v-list tile>
            <v-list-item
              v-for="(conf_item_value, conf_item_key) in getCleanValues(
                conf_value
              )"
              :key="conf_item_key"
              tile
              style="margin-top: 15px; margin-bottom: 15px"
            >
              <!-- label -->
              <v-label
                v-if="
                  conf_item_value['entry_type'] == 'label' &&
                  !conf_item_value['hidden']
                "
                >{{ conf_item_value["label"] }}</v-label
              >
              <!-- boolean value: toggle switch -->
              <v-switch
                v-if="conf_item_value['entry_type'] == 'boolean'"
                v-model="conf_item_value['value']"
                :placeholder="conf_item_value['default_value']"
                :label="$t(conf_item_value['label'])"
                :hint="$t(conf_item_value['description'])"
                :disabled="getDisabledState(conf_value, conf_item_value)"
                @change="
                  saveConfig(
                    configKey,
                    conf_key,
                    conf_item_key,
                    conf_item_value['value']
                  )
                "
              ></v-switch>
              <!-- textbox with single value -->
              <v-text-field
                v-if="
                  (conf_item_value['entry_type'] == 'string' ||
                    conf_item_value['entry_type'] == 'integer' ||
                    conf_item_value['entry_type'] == 'float') &&
                  conf_item_value['options'] === null &&
                  conf_item_value['range'] === null
                "
                v-model="conf_item_value['value']"
                :placeholder="ensureString(conf_item_value['default_value'])"
                :label="$t(conf_item_value['label'])"
                :hint="$t(conf_item_value['description'])"
                :disabled="getDisabledState(conf_value, conf_item_value)"
                filled
                @change="
                  saveConfig(
                    configKey,
                    conf_key,
                    conf_item_key,
                    conf_item_value['value']
                  )
                "
              ></v-text-field>
              <!-- password value -->
              <v-text-field
                v-if="conf_item_value['entry_type'] == 'password'"
                v-model="conf_item_value['value']"
                :placeholder="conf_item_value['default_value']"
                :label="$t(conf_item_value['label'])"
                :hint="$t(conf_item_value['description'])"
                :disabled="getDisabledState(conf_value, conf_item_value)"
                type="password"
                filled
                @change="
                  saveConfig(
                    configKey,
                    conf_key,
                    conf_item_key,
                    conf_item_value['value']
                  )
                "
              ></v-text-field>
              <!-- value with dropdown -->
              <v-select
                v-if="
                  conf_item_value['options'] !== null &&
                  conf_item_value['range'] === null
                "
                v-model="conf_item_value['value']"
                :chips="conf_item_value['multi_value']"
                clearable
                :multiple="conf_item_value['multi_value']"
                :items="conf_item_value['options']"
                :placeholder="ensureString(conf_item_value['default_value'])"
                :label="$t(conf_item_value['label'])"
                :hint="$t(conf_item_value['description'])"
                :disabled="getDisabledState(conf_value, conf_item_value)"
                filled
                @change="
                  saveConfig(
                    configKey,
                    conf_key,
                    conf_item_key,
                    conf_item_value['value']
                  )
                "
              ></v-select>
              <!-- value with range -->
              <v-slider
                v-if="conf_item_value['range'] !== null"
                v-model="conf_item_value['value']"
                :placeholder="ensureString(conf_item_value['default_value'])"
                :label="$t(conf_item_value['label'])"
                :hint="$t(conf_item_value['description'])"
                :disabled="getDisabledState(conf_value, conf_item_value)"
                :min="conf_item_value['range'][0]"
                :max="conf_item_value['range'][1]"
                @change="
                  saveConfig(
                    configKey,
                    conf_key,
                    conf_item_key,
                    conf_item_value['value']
                  )
                "
                ><template #append>
                  <v-text-field
                    v-model="conf_item_value['value']"
                    class="mt-0 pt-0"
                    type="number"
                    style="width: 60px"
                  ></v-text-field>
                </template>
              </v-slider>
              <!-- special: dict -->
              <v-switch
                v-if="
                  conf_item_value['entry_type'] == 'dict' &&
                  conf_key == 'app_tokens'
                "
                value="true"
                :label="$t(conf_item_value['label'])"
                :hint="
                  'Last login: ' +
                  dateStrFromTimestamp(conf_item_value['value']['last_login'])
                "
                persistent-hint
                @change="revokeAppToken(conf_item_key)"
              >
              </v-switch>
            </v-list-item>
          </v-list>
          <v-divider></v-divider>
        </v-list-group>
      </v-list>
    </v-card>
  </section>
</template>

<script>
  import Vue from "vue";
  import { mapGetters, mapState, mapActions } from "vuex";
  export default Vue.extend({
    name: "ConfigView",
    components: {},
    props: {
      configKey: {
        type: String,
        default: "",
      },
    },
    data() {
      return {
        configMenu: {
          base: {
            label: this.$t("config.base"),
            icon: "mdi-tune",
          },
          security: {
            label: this.$t("config.security"),
            icon: "mdi-shield-lock",
          },

          music_providers: {
            label: this.$t("config.music_providers"),
            icon: "mdi-folder-music",
          },

          player_providers: {
            label: this.$t("config.player_providers"),
            icon: "mdi-disc-player",
          },

          metadata_providers: {
            label: this.$t("config.metadata_providers"),
            icon: "mdi-image-search",
          },
          plugins: {
            label: this.$t("config.plugins"),
            icon: "mdi-toy-brick-marker",
          },

          player_settings: {
            label: this.$t("config.player_settings"),
            icon: "mdi-headphones-settings",
          },
        },
      };
    },
    computed: {
      ...mapState(["config"]),
      ...mapGetters(["getProviderIcon", "getPlayer"]),
      configItems() {
        if (!this.config) return {};
        if (this.configKey) {
          return this.config[this.configKey];
        } else return this.config;
      },
    },
    async activated() {
      let windowTitle = this.$t("settings");
      if (this.configKey) {
        windowTitle += " | " + this.$t("config." + this.configKey);
      }
      this.setTopBarOptions({
        title: windowTitle,
        transparent: false,
      });
      if (this.configKey) {
        this.dispatchGetConfigEntries(this.configKey);
      }
    },
    methods: {
      ...mapActions([
        "setTopBarOptions",
        "dispatchGetConfigEntries",
        "setConfigEntryValue",
        "deleteConfigEntry",
      ]),
      async saveConfig(configBase, configKey, valueKey, newValue) {
        if (valueKey === null || valueKey == undefined) {
          this.deleteConfigEntry({
            configBase: configBase,
            configKey: configKey,
          });
        } else {
          this.setConfigEntryValue({
            configBase: configBase,
            configKey: configKey,
            valueKey: valueKey,
            newValue: newValue,
          });
        }
      },
      getDisabledState(confValues, confItemValue) {
        // disable UI elements if main item is disabled or depends_on is set
        if (confItemValue.entry_key === "enabled") {
          return false;
        }
        if (confValues.enabled && !confValues.enabled.value) {
          return true;
        }
        if (
          confItemValue.depends_on &&
          !confValues[confItemValue.depends_on].value
        ) {
          return true;
        }
        return false;
      },
      getCleanValues(confValue) {
        // filter our special cases from a dict
        const newDict = {};
        for (const key in confValue) {
          if (!confValue[key].hidden) {
            newDict[key] = confValue[key];
          }
        }
        return newDict;
      },
      revokeAppToken(clientId) {
        if (confirm("Are you sure you want to revoke this app ?")) {
          this.$server.sendWsCommand("revoke_token", { client_id: clientId });
        }
      },
      dateStrFromTimestamp(timestamp) {
        if (!timestamp) return "never";
        const s = new Date(timestamp * 1000).toLocaleDateString("en-US");
        return s;
      },
      ensureString(val) {
        if (val === undefined || val === null) return "";
        if (typeof val === "number") return val.toString();
        return val;
      },
    },
  });
</script>
